
html * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
html {
    font-size: 62.5%; }
*, *:before, *:after {
    box-sizing: inherit; }
body {
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
    font-family: $primary-font;
    background-color: gold;
    position: relative;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center; }
a {
    text-decoration: none;
    color: $blue; }
img {
    max-width: 100%; }

input, textarea, select {
    font-family: $primary-font;
    font-size: 1.6rem; }

p {
    font-family: $primary-font;
    font-weight: 400;
    color: $gray;
    line-height: 25px; }
h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
    color: black; }

h1 {
    color: gray; }
ul, ol {
    padding-left: 19px; }
main {
    position: relative;
    height: auto; }

.izquierda,
.derecha {
    position: absolute;
    display: inline-block;
    z-index: 999;
    color: blue;
    font-size: 5rem; }

.izquierda {
    left: 50px;
    top: 40%; }

.derecha {
    right: 50px;
    top: 40%; }

.list {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    li {
        width: 100%;
        height: 100vh; } }

.bgi_banner {
    background-size: cover;
    background-position: center center;
    position: absolute; }
.acordeon {
    width: 800px; }

.panel {
    margin: 5px;
    &_head {
        background-color: lightgray;
        padding: 10px;
        border-bottom: 2px solid gray; }
    &_content {
        background-color: silver;
        padding: 10px;
        display: none; } }

main {
    width: 800px;
    padding: 40px;
    background-color: lightblue;
    p {
        margin: 0;
        color: dimgray; }
    a {
        font-weight: bold;
        color: black; } }
.tooltip {
    display: none;
    position: absolute;
    border: 2px solid gray;
    background-color: black;
    border-radius: 5px;
    padding: 10px;
    color: white;
    font-size: 16px;
    box-shadow: 1px 1px 2px gray; }
